@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bungee&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
body {
	margin: 10;
	padding: 0;
	box-sizing: border-box;
	background: #121212;
	font-family: "Nunito", sans-serif;
}

.card__container {
	width: 300px;
	height: 450px;
	background: #8e2de2;

	justify-content: center;
	/* border: 1px solid white; */
	border-radius: 10px;
	margin: 20px 10px;
	color: white;
	transition: all 0.5s ease-in-out;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.card__container:hover {
	transform: scale(1.1);
	transition: all 0.5s ease-in-out;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.cardList__container {
	display: flex;
	flex-wrap: wrap;
	margin: 20px 5px;
	padding: 0 20px;
	justify-content: center;
	align-items: center;
}

.custText {
	height: 45px;
	width: 230px;
	/* text-align: center; */
	text-decoration: none;
	border: none;
	outline: none;
	padding: 0 10px;
	border-radius: 10px;
}
.custText:focus {
	box-shadow: 0 0 0 2.5px blueviolet;
}

.container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	justify-items: center;
	text-align: center;
}
.customtext {
	font-family: "Bungee", cursive;
	font-size: 2rem;
	font-weight: 200;
	color: #720e9e;
}

